.filter_container {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  box-shadow: 3px 1px 14px -5px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 3px 1px 14px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 3px 1px 14px -5px rgba(0, 0, 0, 0.75);

  &.active{
    transform: translateX(0%);
  }
  
  @media screen and (max-width:500px) {
    position: absolute;
    transform: translateX(-85%);
    height: 100%;
    background-color: aliceblue;
    width: 70%;
    z-index: 2;
    transition: all 500ms ease;
    
    .burger_container{
      position: absolute;
      top: 0;
      right: 0;
      padding-top: 0.3rem;
      padding-right: 0.3rem;
      .filter_icon{
        color: #0a5b7e88;
        border: 1px solid #0a5b7e88;
        border-radius: 5px;
      }
    }
  }

  h3 {
    color: gray;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid rgba(223, 223, 223, 0.87);
  }

  .filter_name {
    margin-top: 0.5rem;
    border-bottom: 1px dashed lightgray;

    input:hover {
      cursor: pointer;
    }
    .colors_container {
      display: flex;
      gap: 1rem;
      flex-wrap: wrap;
      padding: 1rem;

      .color {
        width: 30px;
        height: 30px;
        border: 1px solid rgba(128, 128, 128, 0.237);
        border-radius: 5px;
        transition: all 300ms ease;
        &:hover {
          cursor: pointer;
          transform: scale(1.1);
          box-shadow: 0px 0px 15px 2px rgba(31, 252, 225, 0.87);
          -webkit-box-shadow: 0px 0px 15px 2px rgba(31, 252, 225, 0.87);
          -moz-box-shadow: 0px 0px 15px 2px rgba(31, 252, 225, 0.87);
        }
        &.selected {
          border: 4px solid gray;
        }
      }
    }
  }
  .title {
    color: rgb(53, 53, 53);
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 0.5rem;
  }

  .filter_item {
    display: flex;
    align-items: center;
    padding: 2px 20px;
    gap: 1rem;

    input:hover {
      box-shadow: 0px 0px 2px 2px rgba(56, 140, 214, 0.66) inset;
      -webkit-box-shadow: 0px 0px 2px 2px rgba(56, 140, 214, 0.66) inset;
      -moz-box-shadow: 0px 0px 2px 2px rgba(56, 140, 214, 0.66) inset;
    }

    input[type="number"]{
      width: 80px;
      padding: 0.5rem;
      border: 1px solid rgba(128, 128, 128, 0.603);
      border-radius: 5px;
      background-color: transparent;
    }
  }
  .filter_btn_container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    padding: 1rem;
    .btn {
      border-radius: 5px;
      border: 1px solid transparent;
      padding: 0.3rem 1rem;
      background-color: #0a5b7e;
      color: whitesmoke;
      transition: all 300ms ease;
      &:first-child {
        background-color: rgb(105, 105, 105);
        &:hover{
            color:rgb(44, 44, 44);
            border-color: rgb(43, 43, 43);
        }
      }
      &:hover {
        cursor: pointer;
        background-color: transparent;
        border-color: #0a5b7e;
        color: #0a5b7e;
        font-weight: 500;
      }
    }
  }
  .burger_container{
    position: absolute;
    top: 0;
    right: 0;
    padding-top: 0.3rem;
    padding-right: 0.3rem;
    .filter_icon{
      color: #0a5b7e88;
      border: 1px solid #0a5b7e88;
      border-radius: 5px;
    }
  }
}
