
.carts_container{
    height: 100vh;
    position: relative;
    
    @media screen and (max-width:500px) {
        height: auto;
    }
    
    .cart_main{
    padding: 3rem 5rem 3rem;
    display: flex;    
    gap: 1rem;
    height: calc(100vh - 70px);
    overflow: hidden;
    
    @media screen and (max-width:500px) {
        padding: 1rem;
        flex-direction: column;
        height: 100%;
    }

    .cart_summary{
        flex: 3.5;
        border: 1px solid var(--color_primary);
        border-radius: 5px;
        overflow: hidden;
        height: 100%;

    }
    
    .price_details{
        flex: 1.5;
        border: 1px solid var(--color_primary);
        border-radius: 5px;
    }

    .cart_title{
        height: 2.5rem;
        padding: 0.5rem 1rem;
        border-radius: 5px 5px 0 0;
        background-color: var(--color_primary);
        color: whitesmoke;
    }
    .cart_items_container{
        padding: 1rem;
        overflow-y: scroll;
        height: calc(100% - 2.5rem);
        margin-bottom: 2rem;
        
        @media screen and (max-width:500px) {
            margin-bottom: unset;
        }
    }
    .empty_cart{
        display: flex;
        flex-direction: column;
        gap: 2rem;
        flex: 3;
        padding: 5rem;
        align-items: center;
        
        @media screen and (max-width:500px) {
            padding: 1rem;
        }

        .sad_icon{
            font-size: 100px;
            color: rgba(155, 153, 153, 0.658);
            display: flex;
            justify-content: center;
            width: 100%;
        }

        h2, h3{
            color: rgba(155, 153, 153, 0.658);
        }
        
        button:last-of-type{
            background-color: var(--color_primary);
            padding: 0.5rem 1rem;
            color: whitesmoke;
            border: 1px solid transparent;
            border-radius: 5px;
            transition: all 300ms ease;
            
            &:hover{
                color: var(--color_primary);
                background-color: transparent;
                border-color: var(--color_primary);

            }
        }
    }
    
}

}