.error_modal {
  display: block;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.288);
  backdrop-filter: blur(1px);
  position: absolute;
  top: 0;

  transition: all 500ms ease;

  .error {
    position: relative;
    width: max-content;
    top: 15%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid black;
    padding: 3rem;
    z-index: 200;
    border-radius: 1rem;
    background-color: rgba(255, 255, 255, 0.822);
    border: 1px solid transparent;
    color: rgb(255, 47, 47);
    // color: white;

    @media screen and (max-width:500px) {
      width: 80%;
      top: 40%;
      text-align: center;
    }

    .btn{
        position: absolute;
        right: 1rem;
        top: 1rem;
        padding: 0.3rem;
        border-radius: 5px;
        border: 1px solid transparent;
        background-color: tomato;
        color: white;

        &:hover{
            border-color: red;
            color: red;
            background-color: transparent;
            cursor: pointer;
        }
    }
  }
}


