.home_container{
    position: relative;
    overflow-y: hidden;
}
.main{
    display: flex;
    height: calc(100vh - 70px) ;
}

.filter_container{
    flex: 1;
}

.items_container{
    padding: 1rem;
    flex: 4;
    max-width: 80%;
    overflow-y: scroll;
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    gap: 20px;
    height: 100%;


    &::-webkit-scrollbar{
        background-color: transparent;
        width: 0.5rem;
    }
    &::-webkit-scrollbar-thumb{
        background-color: #0a5b7e94;
        border-radius: 1rem;
    }

    @media screen and (max-width:500px) {
        gap: 1rem;
        max-width: 100%;
        align-self: flex-end;
        justify-content: flex-end;
    }
}
