@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");

.product_card {
  // flex: 1;
  // flex-grow: 1;
  width: 220px;
  height: max-content;
  position: relative;
  border-radius: 5% 5% 0 5%;
  overflow: hidden;
  box-shadow: 4px 6px 14px -5px rgba(0, 0, 0, 0.66);
  -webkit-box-shadow: 4px 6px 14px -5px rgba(0, 0, 0, 0.66);
  -moz-box-shadow: 4px 6px 14px -5px rgba(0, 0, 0, 0.66);
  
  @media screen and (max-width:500px) {
    width: 160px;
  }
}

.img_div {
  // flex:2;
  aspect-ratio: 1/0.7;
  border-radius: 10% 10% 0 0;
  overflow: hidden;

  img {
    width: 100%;
    object-fit: cover;
    transition: all 300ms ease;

    &:hover {
      transform: scale(1.1);
    }
  }
}


.product_details {
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.301);
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition: transform 300ms ease;
  backdrop-filter: blur(4px);
  
  @media screen and (max-width:500px) {
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.301);
  }
}

.product_details:hover {
  transform: translateY(-10px);
  box-shadow: -7px -19px 36px -21px rgba(0, 0, 0, 0.62);
  -webkit-box-shadow: -7px -19px 36px -21px rgba(0, 0, 0, 0.62);
  -moz-box-shadow: -7px -19px 36px -21px rgba(0, 0, 0, 0.62);
}

.price_buy {
  margin-top: 10px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Bebas Neue", cursive;
  .price {
    font-size: 20px;
    span {
      font-family: "Bebas Neue", cursive;
      font-size: 30px;
      
      @media screen and (max-width:500px) {
        font-size: 20px;      
      }
    }
  }

  button {
    padding: 5px;
    border: none;
    border-radius: 10px;
    background-color: #069a8e;
    color: #f7ff93;

    &:hover {
      cursor: pointer;
      transform: scale(1.05);
    }
  }
}

