.price_details{
    position: relative;
    
    .items{
        display: flex;
        padding: 1rem;
        flex-direction: column;
        gap: 1rem;
        border-bottom: 2px solid rgba(128, 128, 128, 0.342);

        .summary_item{
            display: flex;
            justify-content: space-between;

            @media screen and (max-width:500px) {
                h2,h3{
                    font-size: 20px;
                }
            }
        }
        .disc_icon{
            display: flex;
            align-items: center;
            color: green;
            gap: 2rem;
            font-weight: 500;
            font-size: 20px;
        }
    }
    .discount{
        border: none;
    }

    .total{
        border: none;
        border-top: 2px solid rgba(128, 128, 128, 0.342);
        position: absolute;
        bottom: 0;
        width: 100%;

        @media screen and (max-width:500px) {
            position: relative;
        }
        
        h3{
            font-size: 25px;
        }

        .order_btn{
            background-color: rgb(255, 123, 0);
            width: 100%;
            height: 3rem;
            border-radius: 5px;
            color: whitesmoke;
            font-weight: 500;
            font-size: 25px;
            border: 2px solid rgb(255, 123, 0);
            transition: all 300ms ease;
        
            &:hover{
                cursor: pointer;
                background-color: transparent;
                color: rgb(255, 123, 0);
            }
        }
    }
}