.success_main{
    padding: 5rem;
    
    @media screen and (max-width:500px) {
        padding: 1rem;
        text-align: center;
    }

    .main_container{
        width: 100%;
        padding: 5rem;
        padding-top: 1rem;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
        border: 1px solid var(--color_primary);
        border-radius: 5px;

        @media screen and (max-width:500px) {
            padding: 1rem;
        }

        .success_icon{
            font-size: 100px;
            color: rgb(5, 187, 5);
        }


        .shop_more_btn, .shopmore_btn{
            background-color: rgb(255, 123, 0);
            width: 100%;
            height: 3rem;
            border-radius: 5px;
            color: whitesmoke;
            font-weight: 500;
            font-size: 25px;
            border: 2px solid rgb(255, 123, 0);
            transition: all 300ms ease;
            padding: auto 2rem;
            margin-right:2rem;
            
            &:hover{
                cursor: pointer;
                background-color: transparent;
                color: rgb(255, 123, 0);
            }
        }
        .shopmore_btn{
            border: none;
            width: max-content;
            padding: auto 2rem;            
            margin-top: 2rem;

            @media screen and (max-width:500px) {
                padding: auto 0rem;          
            }
        }

        .order_progress_container{
            margin-top: 1rem;
            display: flex;
            width: 70%;
            align-items: center;

            @media screen and (max-width:500px) {
                width: 100%;
            }

            hr{
                height: 0px;
                width: 100%;
                background-color: transparent;
                border-bottom: 3px dashed gray;
            }

            .progress{
                width: 600px;
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.5rem;

                .circle{
                    width: 20px;
                    height: 20px;
                    border: 2px solid rgba(168, 168, 168, 0.747);
                    border-radius: 50%;                    
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    
                    &.success{
                        width: 20px;
                        height: 20px;
                        border: 2px solid green;                        
                        animation: blink 1s ease-in-out infinite alternate;
                        .inner_dot{
                            background-color: green;
                        }
                    }
                    .inner_dot{
                        width: 8px;
                        height: 8px;
                        border-radius: 50%;                    
                        background-color:rgb(168, 168, 168);
                    }
                }
            }
        }
    }
}

@keyframes blink {
    from {
        border-color: rgba(0, 128, 0, 0.288);
    }    
    to {
        border-color: rgba(0, 128, 0, 1);
    }    
}