.cart_item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    border-radius: 1rem;
    gap: 1rem;
    box-shadow: 4px 6px 14px -5px rgba(0, 0, 0, 0.66);
    -webkit-box-shadow: 4px 6px 14px -5px rgba(0, 0, 0, 0.66);
    -moz-box-shadow: 4px 6px 14px -5px rgba(0, 0, 0, 0.66);

    .item_img{
        // flex: 1;
        width: 100px;
        height: 100px;
        border-radius: 10px;
        object-fit: contain;
        border: 1px solid lightgray;
    }

    .cart_item_details{
        flex: 4;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            .qty_container{
                display: flex;
                align-items: center;
                justify-content: space-around;
                gap: 0.5rem;

                .qty_btn{
                    padding: 0.3rem;
                    border-radius: 50%;
                    width: 30px;
                    height: 30px;
                    font-size: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: 1px solid gray;
                    background-color: transparent;
                    

                    &:hover{
                        cursor: pointer;
                    }
                }
            }

            .qty{
                border: 1px solid gray;
                padding: 0.5rem;
                aspect-ratio: 4/1.5;
                border-radius: 5px;
                text-align: center;
            }
        }

        .delete_btn{
            color: rgba(0, 0, 0, 0.338);
            outline: none;
            border-radius: 5px;
            border: none;
            transition: all 300ms ease;
            background-color: transparent;
            
            &:hover{
                color: red;
                cursor: pointer;
            }
        }

    }



}