@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");

header {
  position: sticky;
  top: 0px;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #0a5b7e;
  padding: 10px 50px;

  @media screen and (max-width: 500px) {
    padding: 10px 20px;
  }

  .logo {
    border: 2px solid rgb(255 69 105 / 70%);
    border-radius: 15%;
    padding: 2px 14px;
    letter-spacing: 1px;
    font-size: 20px;
    font-family: "Pacifico", cursive;
  }
  
  .search_bar {
    display: flex;
    align-items: center;
    width: 50%;
    gap: 0.5rem;
    border: 1px solid lightgray;
    padding: 5px 15px;
    border-radius: 10px;
    @media screen and (max-width: 500px) {
      width: 60%;
      border: none;
    }

    input {
      width: 100%;
      padding: 5px;
      border: none;
      outline: none;
      background: transparent;
      color: whitesmoke;
    }

    #search::-webkit-search-cancel-button {
      color: red;
      border: 2px solid red;
      background-color: red;
    }
  }

  .cart_icon_container {
    position: relative;
    &:hover {
      cursor: pointer;
    }

    small {
      width: 20px;
      text-align: center;
      background-color: rgb(244 45 45);
      color: white;
      padding: 5px;
      border-radius: 50%;
      position: absolute;
      top: -11px;
      right: -10px;
    }
  }
  .icon {
    color: whitesmoke;
    cursor: pointer;
    text-decoration: none;

    &.blow {
      animation: blow 300ms ease-in-out 1 alternate;
    }
  }
}

@keyframes blow {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.4);
  }
}
